              transform: rotateY(180deg); }
  .card-front {
    -webkit-transform: rotateY(-180deg) translateZ(1px);
            transform: rotateY(-180deg) translateZ(1px);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
